* {
  padding: 0;
  margin: 0;
}
#root {
  width: 1900px;
  height: 100%;
  background: #f5f5f5;
}
.title {
  width: 240px;
  height: 60px;
  line-height: 60px;
  color: #fff;
  background: #001224;
  text-align: center;
  font-size: 24px;
  margin-bottom: 0;
}
.menu {
  float: left;
  width: 240px;
  min-height: 800px;
  background: #001224;
}
.header {
  width: 100%;
  height: 60px;
  line-height: 60px;
}
.header .right {
  float: right;
  margin-right: 40px;
}
