.App {
  width: 100%;
  height: 100%;
  background: #fff;
  padding-top: 100px;
}
.App .login-wrap {
  width: 682px;
  height: 487px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.16);
  margin: 0px auto;
  background: #fff;
}
.App .login-wrap .left {
  float: left;
  width: 262px;
  height: 100%;
  background: #1770E8;
}
.App .login-wrap .right {
  float: left;
  width: 420px;
  height: 100%;
  padding-left: 58px;
}
.App .login-wrap .right h3 {
  padding-top: 79px;
  text-align: center;
  line-height: 40px;
  font-size: 28px;
  color: #0092F1;
}
.App .login-wrap .right .p1 {
  margin-top: 50px;
}
.App .login-wrap .right .p2 {
  width: 305px;
}
.App .login-wrap .right .p3 {
  margin-top: 32px;
}
.App .login-wrap .right .p4 {
  width: 305px;
  margin-top: 37px;
}
